                console.log(`[Reset Password] ‚ö†Ô∏è Email failed. DEV MODE: Reset URL is ${resetUrl}`);
                return { success: false, message: `„É°„Éº„É´ÈÄÅ‰ø°„Ç®„É©„Éº: ${error.message}` };
