// 2. Reset Password
export async function resetPasswordAction(formData: FormData) {
    const token = formData.get('token') as string;
    const password = formData.get('password') as string;

    if (!token || !password) {
        return { success: false, message: 'ÁÑ°Âäπ„Å™„É™„ÇØ„Ç®„Çπ„Éà„Åß„Åô„ÄÇ' };
    }

    const users = getUsers();
    const userIndex = users.findIndex((u: any) => u.resetToken === token);
    const user = users[userIndex];

    if (!user) {
        return { success: false, message: 'ÁÑ°Âäπ„Å™„Åæ„Åü„ÅØÊúüÈôêÂàá„Çå„ÅÆ„É™„É≥„ÇØ„Åß„Åô„ÄÇ' };
    }

    // Check expiry
    if (user.resetTokenExpiry && user.resetTokenExpiry < Date.now()) {
        return { success: false, message: '„É™„É≥„ÇØ„ÅÆÊúâÂäπÊúüÈôê„ÅåÂàá„Çå„Å¶„ÅÑ„Åæ„Åô„ÄÇ„ÇÇ„ÅÜ‰∏ÄÂ∫¶„É™„ÇØ„Ç®„Çπ„Éà„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ' };
    }

    // Update Password
    user.password = password;
    // Clear token
    delete user.resetToken;
    delete user.resetTokenExpiry;

    users[userIndex] = user;
    saveUsers(users);

    return { success: true, message: '„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂ§âÊõ¥„Åó„Åæ„Åó„Åü„ÄÇ„É≠„Ç∞„Ç§„É≥„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ' };
}
